import axios from 'axios'
export const ADD_TO_COMPARE = 'ADD_TO_COMPARE'
export const DELETE_FROM_COMPARE = 'DELETE_FROM_COMPARE'
export const DO_A_BIT = 'DO_A_BIT'

export const doABit = (item) => {
  return dispatch => {
    axios
      .get('https://jsonplaceholder.typicode.com/todos/')
      .then(response => {
        dispatch({ type: 'DO_A_BIT', payload: response.data })
      })
      .catch(err => {
        console.log('Error: ', err)
      })
  }
}

// add to compare
export const addToCompare = (item, addToast) => {
  return dispatch => {
    if (addToast) {
      addToast('Added To Compare', {
        appearance: 'success',
        autoDismiss: true
      })
    }
    dispatch({ type: ADD_TO_COMPARE, payload: item })
  }
}

// delete from compare
export const deleteFromCompare = (item, addToast) => {
  return dispatch => {
    if (addToast) {
      addToast('Removed From Compare', {
        appearance: 'error',
        autoDismiss: true
      })
    }
    dispatch({ type: DELETE_FROM_COMPARE, payload: item })
  }
}
